section.studiesSection {
    margin: 5% 50px;
}

.studiesSection h2{
    width: 100%;
    text-align: center;
    font-size: 40px;
}

.studiesSection h4{
    width: 90%;
    margin: 0px 4% 25px 6%;
    text-align: left;
    font-size: 30px;
}

.studiesSection p{
    width: 90%;
    font-size: 20px;
    margin: 50px auto;
    text-align: justify;
}

.studiesSection div.container{
    width: 90%;
    margin: 25px auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
}

.studiesSection div.school{
    width: 48%;
    margin: 10px 1%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 3px solid var(--box-color);
    border-radius: 10px;
    padding: 25px;
}

.studiesSection div.school ul{
    width: 70%;
    list-style: none;
}

.studiesSection div.school ul li{
    font-size: 20px;
    padding: 5px;
}

.studiesSection div.school img{
    width: 150px;
    height: 150px;
    object-fit: contain;
}

.studiesSection div.languaje{
    margin: 10px 1%;
    width: 23%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid var(--box-color);
    border-radius: 10px;
    padding: 25px;
}

.studiesSection div.languaje span{
    display: inline-block;
    width: 75%;
    text-align: center;
    font-size: 20px;
}

.studiesSection div.languaje span b{
    font-size: 24px;
}

.studiesSection div.languaje img{
    width: 75px;
    height: 75px;
    border-radius: 50%;
    object-fit: cover;
    
}

.studiesSection a.certification{
    margin: 10px 1%;
    width: 23%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    border: 3px solid var(--box-color);
    border-radius: 10px;
    padding: 10px;
    color: var(--text-color);
    text-decoration: none;
    position: relative;
}

.studiesSection a.certification img{
    width: 128px;
    height: 128px;
    margin: 12px;
    object-fit: cover;
}

.studiesSection a.certification span{
    display: inline-block;
    width: 100%;
}

.studiesSection a.certification span.name{
    font-size: 20px;
    margin: 5px 10px;
}

.studiesSection a.certification span.date{
    font-size: 12px;
    margin: 5px 10px;
    color: (--box-color);
}

.studiesSection a.certification i{
    position: absolute;
    bottom: 25px;
    right: 25px;
    font-size: 16px;
    margin: 0;
    padding: 0;
}

.studiesSection div.course{
    width: 48%;
    margin: 10px 1%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 3px solid var(--box-color);
    border-radius: 10px;
    padding: 25px;
}

/* Big devices (from 960 to 1360) */
@media only screen and (min-width: 960px) and (max-width: 1360px) {
    .studiesSection h2{
        font-size: 40px;
        margin-bottom: 25px;
    }
    
    .studiesSection h4{
        margin: 0 1%;
        font-size: 26px;
    }

    .studiesSection div.container{
        width: 100% !important;
        margin: 25px auto;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
    }

    .studiesSection div.school ul{
        width: 75%;
    }

    .studiesSection div.school ul li{
        font-size: 16px;
        padding: 5px;
    }

    .studiesSection div.school img{
        width: 100px;
        height: 100px;
    }

    .studiesSection div.languaje{
        margin: 10px 1%;
        width: 48%;
    }

    .studiesSection div.languaje span{
        display: inline-block;
        width: 75%;
        text-align: center;
        font-size: 20px;
    }

    .studiesSection div.languaje span b{
        font-size: 24px;
    }

    .studiesSection div.languaje img{
        width: 60px;
        height: 60px;
    }

    .studiesSection a.certification{
        margin: 10px 1%;
        width: 48%;
        border-radius: 10px;
        padding: 10px;
    }
    
    .studiesSection a.certification span{
        display: inline-block;
        width: 100%;
    }
    
    .studiesSection a.certification span.name{
        font-size: 16px;
        margin: 5px 10px;
    }
    
    .studiesSection a.certification span.date{
        font-size: 12px;
        margin: 5px 10px;
    }
    
    .studiesSection a.certification i{
        font-size: 14px;
    }
    
    .studiesSection div.course{
        width: 48%;
        margin: 10px 1%;
        padding: 15px;
        font-size: 16px;
    }
}

