#routerDiv{
    width: 100%;
    display: flex !important;
    justify-content: space-between;
    align-items: flex-start;
    flex-flow: row;
}

nav{
    width: 20%;
    height: auto;
    top: 5%;
    position: sticky;
    overflow: hidden;
}

nav ul{
    width: 90%;
    height: 100%;
    margin: 25px auto;
    padding: 25px;
    border-radius: 5px;
    list-style: none;
    background: var(--box-color);
}

nav ul li{
    width: auto;
    color: var(--text-color);
    margin: 25px 0;
}

.menuLink{
    display: inline-block;
    width: 100%;
    color: var(--text-color);
    text-decoration: none;
    font-size: 32px;
}

.menuLink:hover{
    transform: scale(1.05);
    opacity: 0.75;
}

.menuLink i{
    font-size: 24px;
    width: 30px;
    margin: 10px 15px;
    text-align: center;
}

.mainContainer{
    width: 80%;
    min-height: 100vh;
}

.mobileToogleMenu{
    width: 100%;
    position: sticky;
    top: 0px;
    border: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 15px;
    background: transparent;
    z-index: 1000;
    color: var(--text-color);
}

/* Big devices (from 960 to 1360) */
@media only screen and (min-width: 960px) and (max-width: 1360px) {
    #routerDiv{
        width: 100%;
        display: flex !important;
        justify-content: space-between;
        align-items: flex-start;
        flex-flow: row;
    }
    
    nav{
        width: 30%;
        height: auto;
        top: 5%;
        position: sticky;
        overflow: hidden;
    }
    
    nav ul{
        width: 90%;
        height: 100%;
        margin: 25px auto;
        padding: 25px;
        border-radius: 5px;
        list-style: none;
    }
    
    nav ul li{
        width: auto;
        margin: 25px 0;
    }
    
    .menuLink{
        width: 100%;
        font-size: 24px;
    }
    
    .mainContainer{
        width: 70%;
        min-height: 100vh;
    }
}

