
button{
    width: 100%;
    background: var(--bg-color);
    border: 1px solid var(--text-color);
    color: var(--text-color);
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    transition: all 0.25s;
}

button:hover{
    transform: scale(1.05);
}

.sun{
    background-image: url('../../img/ui/sky.png');
    object-fit: cover;
}

.moon{
    background-image: url('../../img/ui/star.jpg');
    object-fit: cover;
}

button i{
    font-size: 32px;
    width: 32px;
    margin: 10px;
}

i.fa-sun{
    color: rgb(255, 238, 0);
}