.skillScale{
    position: absolute;
    width: 50%;
    top: -100px;
    right: 25px;
    background: var(--bg-color);
    border: 1px solid var(--text-color);
    padding: 15px;
    border-radius: 25px 25px 0 25px;
}

.skillScale small{
    font-size: 15px;
    width: 100%;
    padding: 15px;
    text-align: center;    
    margin: 0;
}

.skillLevelBar{
    width: 100%;
    height: 10px;
    margin-top: 25px;
    position: relative;
    background: rgba(255,202,152,0.5);
    background: -moz-linear-gradient(left, rgba(255,202,152,0.5) 0%, rgba(255,249,132,0.5) 25%, rgba(199,255,115,0.5) 50%, rgba(109,204,228,0.5) 75%, rgba(86,152,214,0.5) 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(255,202,152,0.5)), color-stop(25%, rgba(255,249,132,0.5)), color-stop(50%, rgba(199,255,115,0.5)), color-stop(75%, rgba(109,204,228,0.5)), color-stop(100%, rgba(86,152,214,0.5)));
    background: -webkit-linear-gradient(left, rgba(255,202,152,0.5) 0%, rgba(255,249,132,0.5) 25%, rgba(199,255,115,0.5) 50%, rgba(109,204,228,0.5) 75%, rgba(86,152,214,0.5) 100%);
    background: -o-linear-gradient(left, rgba(255,202,152,0.5) 0%, rgba(255,249,132,0.5) 25%, rgba(199,255,115,0.5) 50%, rgba(109,204,228,0.5) 75%, rgba(86,152,214,0.5) 100%);
    background: -ms-linear-gradient(left, rgba(255,202,152,0.5) 0%, rgba(255,249,132,0.5) 25%, rgba(199,255,115,0.5) 50%, rgba(109,204,228,0.5) 75%, rgba(86,152,214,0.5) 100%);
    background: linear-gradient(to right, rgba(255,202,152,0.5) 0%, rgba(255,249,132,0.5) 25%, rgba(199,255,115,0.5) 50%, rgba(109,204,228,0.5) 75%, rgba(86,152,214,0.5) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffca98', endColorstr='#5698d6', GradientType=1 );
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.skillLevelBar ol{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    font-size: 14px;
    position: absolute;
    top: -50px;
}

@media only screen and (min-width: 320px) and (max-width: 720px){
    .skillScale{
        display: flex;
        align-items: flex-end;
        position: absolute;
        width: 100%;
        top: -100px;
        right: 0px;
        background: var(--bg-color);
        border: 1px solid var(--text-color);
        padding: 25px 5px;
        border-radius: 25px 25px 0 25px;
    }

    .skillScale small{
        font-size: 15px;
        width: 75%;
        padding: 5px;
        text-align: left;    
        margin: 0;
    }
    
    .skillLevelBar ol{
        width: 25%;
        height: 100px;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        align-items: flex-start;
        padding: 25px;
        font-size: 14px;
        position: absolute;
        top: -130px;
    }
}