div.blackScreen{
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.85);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
}

div.fullCover{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    display: flex;
    justify-content: center;
    align-items: center;
}

div.project{
    width: 50%;
    background: var(0, 0, 0, 0.85);
    border-radius: 25px;
    position: relative;
    z-index: 100;
    display: flex;
    flex-flow: row wrap;
    position: relative;
    overflow: hidden;
}

div.projectInfo{
    width: 100%;
    padding: 50px;
    background: var(--bg-color);
}

div.project button.close{
    position: absolute;
    top: 25px;
    right: 25px;
    color: rgba(255, 255, 255, 1);
    text-shadow: 2px 2px rgba(127, 0, 0, 1);
    border: 0;
    width: 25px;
    height: 25px;
    font-size: 20px;
    background: transparent;
}

img.banner{
    width: 100%;
    height: 400px;
    border-radius: 25px 25px 0 0;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
    filter: blur(25px);
    padding-bottom: 10px;
}

div.bannerCover{
    width: 100%;
    height: 400px;
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 25px 25px 0 0;
}

img.bannerFull{
    width: 80%;
    height: 350px;
    left: 0;
    margin: 0 10%;
    object-fit: contain;
    object-position: center;
    position: absolute;
    top: 25px;
}

span.projectName{
    width: 100%;
    font-size: 24px;
    text-align: center;
}

span.projectDescription{
    display: inline-block;
    width: 100%;
    font-size: 16px;
    margin: 10px 0;
    text-align: justify;
}

ul.projectTools{
    width: 100%;
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    align-items: center;
}

ul.projectTools li{
    width: auto;
    padding: 10px !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

ul.projectTools li img{
    width: 75%;
    max-width: 75px;
    object-fit: contain;
}

small{
    display: inline-block;
    width: 100%;
    margin-top: 25px;
    font-size: 12px;
}


