section.experienceSection{
    margin: 5% 50px;
}

.experienceSection h2{
    width: 100%;
    text-align: center;
    font-size: 40px;
}

.experienceSection .logo{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 5px solid var(--box-color);
    position: absolute;
    top: 25px;
    right: 25px;
}

.experienceSection h4.position{
    width: 100%;
    margin: 0px auto;
    text-align: left;
    font-size: 30px;
}

.experienceSection h6{
    width: 100%;
    font-size: 22px;
    text-align: center;
    margin: 15px 0;
}

.experienceSection p{
    width: 90%;
    font-size: 20px;
    margin: 50px auto;
    text-align: justify;
}

.experienceSection h5.company{
    width: 100%;
    height: 50px;
    margin: 0px auto 10px auto;
    text-align: left;
    font-size: 24px;
    opacity: 0.75;
}

.experienceSection span.dates{
    display: inline-block;
    margin: 25px;
    font-size: 16px;
    font-weight: lighter;
    letter-spacing: 1px;
}

.experienceSection div.container{
    width: 90%;
    margin: 25px auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    position: relative;
}

.experienceSection .work{
    margin: 25px 0;
    border: 3px solid var(--box-color);
    padding: 25px;
    border-radius: 25px;
}

.experienceSection ul{
    list-style: none;
}

.experienceSection div.container ul li{
    padding: 5px 15px;
}

.experienceSection ul.projectList{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
}

.experienceSection ul.projectList li{
    width: 100%;
    cursor: pointer;
    transition: transform 0.25s;
    font-size: 16px;
}

.experienceSection ul.projectList li:hover{
    transform: scale(1.01);
    opacity: 0.75;
}

.experienceSection span.projectName{
    display: inline-block;
    width: 100%;
}

.experienceSection .project i{
    margin: 5px 15px;
}


