header{
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

header img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
}

.cover{
    width: 0;
    height: 0;
    position: absolute;
    z-index: 0;
}

.firtsCover{
    top: -60%;
    left: 0;
    border-left: 60vw solid transparent;
    border-top: 60vh solid transparent;
    border-right: 120vw solid var(--bg-color);
    border-bottom: 120vh solid var(--bg-color);
    opacity: 0.5;
}

.secondCover{
    top: 10%;
    left: -60%;
    border-left: 60vw solid transparent;
    border-top: 60vh solid transparent;
    border-right: 120vw solid var(--bg-color);
    border-bottom: 120vh solid var(--bg-color);
    opacity: 0.75;
}

.thirdCover{
    border-left: 50vw solid transparent;
    border-top: 50vh solid transparent;
    border-right: 50vw solid var(--bg-color);
    border-bottom: 50vh solid var(--bg-color);
    opacity: 1;
}

.profile{
    width: 400px;
    height: 400px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    top: 10%;
    left: 10%;
    border: 10px solid var(--box-color);
    z-index: 25;
}

.presentation{
    width: 60%;
    height: 60%;
    bottom: 0;
    right: 0;
    position: absolute;
    padding: 100px;
}

.presentation h1{
    width: 100%;
    text-align: right;
    font-size: 5rem;
    margin-bottom: 100px;
    color: var(--text-color);
}

.presentation h3{
    width: 100%;
    text-align: right;
    font-size: 3rem;
    margin: 25px;
    color: var(--text-color);
}

.presentation h5{
    width: 100%;
    text-align: right;
    font-size: 1.5rem;
    margin: 25px;
    color: var(--text-color);
}

/* Big devices (from 960 to 1360) */
@media only screen and (min-width: 960px) and (max-width: 1360px) {
    .profile{
        width: 400px;
        height: 400px;
    }

    .presentation h1{
        margin-bottom: 50px;
    }
    
    .presentation h3{
        margin: 15px 0;
    }
    
    .presentation h5{
        margin: 15px 0;
    }
}

/* Middle devices (from 720 to 960) */
@media only screen and (min-width: 720px) and (max-width: 960px) {
    .secondCover{
        opacity: 0.9;
    }

    .thirdCover{
        display: none;
    }

    .profile{
        width: 300px;
        height: 300px; 
        top: 5%;
        left: 5%;
    }

    .presentation{
        width: 80%;
        height: 50%;
        padding: 50px;
    }

    .presentation h1{
        margin-bottom: 20px;
    }
    
    .presentation h3{
        margin: 15px 0;
        font-size: 2.5rem;
    }
    
    .presentation h5{
        margin: 15px 0;
        font-size: 1.5rem;
    }
}

/* Small devices (from 640 to 720) */
@media only screen and (min-width: 640px) and (max-width: 720px){
    .secondCover{
        opacity: 0.9;
    }

    .thirdCover{
        display: none;
    }

    .profile{
        width: 300px;
        height: 300px; 
        top: 5%;
        left: 5%;
    }

    .presentation{
        width: 80%;
        height: 50%;
        padding: 50px;
    }

    .presentation h1{
        margin-bottom: 20px;
    }
    
    .presentation h3{
        margin: 15px 0;
        font-size: 2.5rem;
    }
    
    .presentation h5{
        margin: 15px 0;
        font-size: 1.5rem;
    }
}

/* Extra small devices (from 360 to 640) */
@media only screen and (min-width: 360px) and (max-width: 640px){
    .secondCover{
        opacity: 0.9;
    }

    .thirdCover{
        display: none;
    }

    .profile{
        width: 250px;
        height: 250px; 
        top: 5%;
        left: 5%;
    }

    .presentation{
        width: 100%;
        height: 40%;
        padding: 10px;
    }

    .presentation h1{
        font-size: 3rem;
        margin-bottom: 20px;
    }
    
    .presentation h3{
        margin: 15px 0;
        font-size: 2rem;
    }
    
    .presentation h5{
        margin: 15px 0;
        font-size: 1rem;
    }
}