footer{
    width: 100%;
    background-color: var(--box-color);
    min-height: 160px;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    align-items: center;
    padding: 25px;
}

footer .advice{
    width: 60%;
    border-right: 2px solid var(--text-color);
}

footer .advice p{
    font-size: 12px;
    line-height: 20px;
    padding: 15px 25px;
}

footer h5{
    width: 100%;
    padding: 0 25px;
    font-size: 16px;
}

footer ul{
    list-style: none;
    width: 40%;
    display: flex;
    flex-flow: row wrap;
}

footer ul li{
    margin: 25px;
}

footer ul li a{
    text-decoration: none;
}

footer ul li a i{
    font-size: 40px;
    color: var(--text-color);
}

footer p.version{
    width: 100%;
    font-size: 12px;
    margin: 25px;
}

/* Big devices (from 960 to 1360) */
@media only screen and (min-width: 960px) and (max-width: 1360px) {
    footer .advice{
        width: 60%;
    }

    footer ul{
        width: 40%;
    }

    footer ul li a i{
        font-size: 35px;
        color: var(--text-color);
    }
}

/* Middle devices (from 720 to 960) */
@media only screen and (min-width: 720px) and (max-width: 960px) {
    footer{
        flex-flow: column-reverse wrap;
    }

    footer .advice{
        width: 100%;
        border-right: 0;
    }

    footer ul{
        width: 100%;
    }

    footer ul li a i{
        font-size: 35px;
        color: var(--text-color);
    }
}

/* Small devices (from 640 to 720) */
@media only screen and (min-width: 640px) and (max-width: 720px){
    footer{
        flex-flow: column-reverse wrap;
    }

    footer .advice{
        width: 100%;
        border-right: 0;
    }

    footer ul{
        width: 100%;
    }

    footer ul li a i{
        font-size: 30px;
        color: var(--text-color);
    }
}

/* Extra small devices (from 360 to 640) */
@media only screen and (min-width: 360px) and (max-width: 640px){
    footer{
        flex-flow: column-reverse wrap;
    }

    footer .advice{
        width: 100%;
        border-right: 0;
    }

    footer ul{
        width: 100%;
    }
    
    footer ul li a i{
        font-size: 25px;
        color: var(--text-color);
    }
}