section.resumeSection{
    margin: 5% 50px;
}

.resumeSection h2{
    width: 100%;
    text-align: center;
    font-size: 40px;
}

.resumeSection p{
    width: 90%;
    font-size: 20px;
    margin: 50px auto;
    text-align: justify;
}

.resumeSection .skillContainer {
    width: 90%;
    margin: 50px auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: row wrap;
    position: relative;
}

.resumeSection .order{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 25px 0;
    font-size: 20px;
}

.resumeSection .order .infoBtn{
    width: 50px;
    border: 0;
    font-size: 20px;
}

.resumeSection .order button{
    width: 15%;
    display: flex;
    align-items: center;
    font-size: 16px;
    margin: 5px;
    height: 30px;
    border: 1px solid var(--box-color);
}

.resumeSection .order button i{
    width: 25px;
    font-size: 20px;
    margin: 0;
    padding: 0;
}

.resumeSection .skill {
    width: 18%;
    text-align: center;
    border: 1px solid var(--box-color);
    border-radius: 0;
    padding: 5px;
    margin: 10px 1%;
    background: var(--box-color);
}

.resumeSection .skillBox {
    width: 100%;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 0 25px;
    padding: 10px;
    background: var(--bg-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row wrap;
}

.resumeSection .skill .title{
    display: inline-block;
    width: 100%;
    font-size: 20px;
    margin: 20px;
}

.resumeSection .skill .badge {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 16px;
    height: 24px;
    border-radius: 0 0 0 25px;
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.resumeSection .level1 {
    background: rgba(255, 203, 152, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.resumeSection .level2 {
    background: rgba(255, 249, 132, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.resumeSection .level3 {
    background: rgba(199, 255, 115, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.resumeSection .level4 {
    background: rgba(109, 204, 228, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.25);
}

.resumeSection .level5 {
    background: rgba(86, 152, 214, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.25);
}

