section.softwareSection{
    margin: 5% 50px;
}

.softwareSection h2{
    width: 100%;
    text-align: center;
    font-size: 40px;
}

.softwareSection p{
    margin: 50px auto;
    font-size: 20px;
}

.softwareSection ul.softwareList {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
}

.softwareSection ul.softwareList li {
    width: 20%;
    margin: 25px;
    cursor: pointer;
    background: var(--box-color);
    border-radius: 25px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
}

.softwareSection ul.softwareList li img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 25px 25px 0 0;
}

.softwareSection ul.softwareList h3{
    width: 100%;
    font-size: 20px;
    padding: 10px;
    text-align: center;
    font-weight: normal;
}

.softwareSection ul.softwareList li button{
    width: 120px;
    height: 30px;
    margin: 15px;
    font-size: 15px;
    font-weight: normal;
    background: var(--bg-color);
    color: var(--text-color);
    border-radius: 12.5px;
    border: 0;
    outline: none;
}

